import dayjs from 'dayjs';
import { useCallback, useEffect, useState } from 'react';

import { PuiBox, PuiIcon, PuiSvgIcon } from '../../../ui';
import {
  DateInputContainer,
  ProgressFill,
  StyledArrowContainer,
  StyledPillDateField,
  StyledPillPlaceholder,
} from '../../common/date-picker/StyledComponents';
import type { PuiDateRangeInputProps } from '../types';

export const PuiDateRangePill: React.FC<PuiDateRangeInputProps> = ({
  onFromChange,
  onToChange,
  onFromClick,
  onToClick,
  from,
  to,
  isOpen,
  startDateRef,
  endDateRef,
  placeholder: placeholderValue = '',
}) => {
  const dateDifference = from && to ? to.diff(from, 'day') : 0;
  const dayLabel = dateDifference > 1 ? 'days' : 'day';
  const dateDifferenceString = dateDifference > 0 ? `${dateDifference} ${dayLabel}` : '';
  const editMode = isOpen || from || to;
  const showPlaceholder = !editMode || (!isOpen && dateDifference > 0);
  const format = 'DD/MM';
  const inputPlaceholder = editMode ? format : '';
  const placeholder = dateDifference > 0 ? dateDifferenceString : '';
  const [dateProgress, setDateProgress] = useState(0);

  const calculateProgress = useCallback(() => {
    if (!from || !to) {
      setDateProgress(0);

      return;
    }

    const now = dayjs();

    if (from.isBefore(now) && to.isBefore(now)) {
      setDateProgress(100);

      return;
    }

    let progress = 0;

    if (now.isBefore(to, 'day') || now.isSame(to, 'day')) {
      const fromNowDifference = now.diff(from, 'hour');
      const fromToDifference = to.diff(from, 'hour');
      progress = Math.min((fromNowDifference / fromToDifference) * 100, 100);
    }
    setDateProgress(progress);
  }, [from, to]);

  useEffect(() => {
    calculateProgress();
  }, [from, to, calculateProgress]);

  const classNames = `pill ${!editMode ? 'date-empty' : ''} ${showPlaceholder ? 'show-placeholder' : ''}`;

  return (
    <DateInputContainer className={classNames}>
      <ProgressFill
        style={{ width: `${dateProgress}%` }}
        className={`${isOpen ? 'show-animation' : ''}`}
      />
      <PuiBox className='date-field'>
        <StyledPillDateField
          format={format}
          className='align-right'
          onClick={onFromClick}
          value={from}
          onChange={onFromChange}
          InputProps={{
            placeholder: inputPlaceholder || placeholderValue,
            inputRef: startDateRef,
          }}
        />
        <StyledArrowContainer className='justify-icon-center'>
          {from?.isValid() && to?.isValid() && (
            <PuiSvgIcon
              icon={PuiIcon.ArrowNarrowRight}
              width={6}
              height={6}
            />
          )}
        </StyledArrowContainer>
        <StyledPillDateField
          format={format}
          onClick={onToClick}
          value={to}
          onChange={onToChange}
          InputProps={{
            placeholder: inputPlaceholder,
            inputRef: endDateRef,
          }}
        />
      </PuiBox>
      <StyledPillPlaceholder className='date-placeholder'>{placeholder}</StyledPillPlaceholder>
    </DateInputContainer>
  );
};
