import { useTheme } from '@mui/material';
import type { TimePickerProps } from '@mui/x-date-pickers';
import type { DatePickerProps } from '@mui/x-date-pickers/DatePicker';
import type { DateTimePickerProps } from '@mui/x-date-pickers/DateTimePicker';
import type { PickerValidDate } from '@mui/x-date-pickers/models';
import { pickersLayoutClasses } from '@mui/x-date-pickers/PickersLayout';
import dayjs from 'dayjs';
import timezone from 'dayjs/plugin/timezone';
import utc from 'dayjs/plugin/utc';
import { useEffect, useRef, useState } from 'react';

import type { DateFormatType, DateTimeFormatType, DateValueType, TimeFormatType } from '../config';
import { DATE_TIME_FORMATS, DateFormats, DateTimeFormats } from '../config';
import { formatDateTime } from './format';
import { DateTimeProvider } from './Provider';
import { CustomActionBar, PickerIcon, StyledTextField } from './StyledComponents';

dayjs.extend(utc);
dayjs.extend(timezone);

export default dayjs;

interface IBasePickerProps {
  PickerComponent: React.ElementType;
  format: DateFormatType | DateTimeFormatType | TimeFormatType;
  showDaysOutsideCurrentMonth?: boolean;
  fixedWeekNumber?: number;
  defaultValue?: DateValueType;
  value?: DateValueType;
  autoFocus?: boolean;
  open?: boolean;
  onOpen?: () => void;
  onClose?: () => void;
  onKeyDown?: (event: React.KeyboardEvent) => void;
  focused?: boolean;
  placeholder?: string;
}

type CombinedPickerProps = IBasePickerProps &
  (
    | Omit<DatePickerProps<PickerValidDate>, 'format' | 'value' | 'defaultValue'>
    | Omit<DateTimePickerProps<PickerValidDate>, 'format' | 'value' | 'defaultValue'>
    | Omit<TimePickerProps<PickerValidDate>, 'format' | 'value' | 'defaultValue'>
  );

export function BasePicker({
  PickerComponent,
  format,
  defaultValue,
  value,
  showDaysOutsideCurrentMonth = true,
  fixedWeekNumber = 6,
  autoFocus = false,
  open,
  onOpen,
  onClose,
  focused,
  slots = {},
  slotProps = {},
  onKeyDown,
  placeholder = '',
  ...props
}: CombinedPickerProps) {
  const theme = useTheme();
  const inputRef = useRef<HTMLInputElement>(null);
  const [isFocused, setIsFocused] = useState<boolean>(autoFocus || !!focused);
  const [isOpened, setIsOpened] = useState<boolean>(!!open);

  useEffect(() => {
    setIsFocused(!!focused);
  }, [focused]);

  useEffect(() => {
    setIsOpened(!!open);
  }, [open]);

  const handleTextFieldClick = () => {
    if (isFocused) {
      onOpen ? onOpen() : setIsOpened(true);
    }
  };

  const handleTextFieldFocus = () => {
    setIsFocused(true);
  };

  const handleTextFieldBlur = () => {
    setIsFocused(false);
    !isOpened ? inputRef.current?.blur() : inputRef.current?.focus();
  };

  const handleDatepickerClose = () => {
    onClose ? onClose() : setIsOpened(false);
    inputRef.current?.blur();
  };

  function isDateFormat(format: string): format is DateFormatType | DateTimeFormatType {
    return format in DateFormats || format in DateTimeFormats;
  }

  return (
    <DateTimeProvider>
      <PickerComponent
        showDaysOutsideCurrentMonth={showDaysOutsideCurrentMonth}
        fixedWeekNumber={fixedWeekNumber}
        format={!isDateFormat(format) ? format : DATE_TIME_FORMATS[format]}
        defaultValue={formatDateTime(defaultValue)}
        value={formatDateTime(value)}
        open={isOpened}
        onClose={handleDatepickerClose}
        autoFocus={autoFocus}
        slots={{
          actionBar: CustomActionBar,
          textField: StyledTextField,
          openPickerIcon: PickerIcon,
          ...slots,
        }}
        slotProps={{
          ...slotProps,
          textField: () => ({
            inputRef: inputRef,
            placeholder: placeholder || undefined,
            InputProps: {
              endAdornment: null,
            },
            sx: !placeholder && {
              '& .MuiInputBase-root input::placeholder': {
                opacity: 0,
                transition: 'opacity 0.1s',
              },
              '& .MuiInputBase-root:hover input::placeholder': {
                opacity: 1,
              },
            },
            onMouseDown: handleTextFieldClick,
            onFocus: handleTextFieldFocus,
            onBlur: handleTextFieldBlur,
            onKeyDown: onKeyDown,
          }),
          openPickerButton: {
            color: 'primary',
            size: 'small',
          },
          layout: {
            sx: {
              [`.${pickersLayoutClasses.contentWrapper}`]: {
                gridColumn: 1,
                gridRow: 1,
              },
              '& .MuiDateCalendar-root .MuiButtonBase-root.MuiPickersDay-today:not(.Mui-selected)': {
                backgroundColor: 'transparent',
                border: `1px solid ${theme.palette.primary.main}`,
              },
              ...(slotProps.layout?.sx || {}),
            },
          },
        }}
        {...props}
      />
    </DateTimeProvider>
  );
}
