import { CheckDueDate, PuiTooltip } from '../../../../ui';
import { ComplededAfterDot, ComplededIcon } from './StyledComponents';

type StatusCompletedIconProps = {
  isCompletedAfter?: boolean;
  value?: number;
};

export const StatusCompletedIcon: React.FC<StatusCompletedIconProps> = ({ isCompletedAfter = false, value }) => {
  const getTooltipText = (): string => {
    const dayText = value === 1 ? 'day' : 'days';

    if (value === 0) {
      return 'Completed on the Due Date';
    }

    if (isCompletedAfter) {
      return `Completed ${value} ${dayText} after the Due Date`;
    }

    return `Completed ${value} ${dayText} pior to Due Date`;
  };

  const tooltipText = getTooltipText();

  return (
    <PuiTooltip
      title={tooltipText}
      placement='top'
      arrow
    >
      <ComplededIcon>
        {isCompletedAfter ? <ComplededAfterDot /> : null}
        <CheckDueDate />
      </ComplededIcon>
    </PuiTooltip>
  );
};
