import type { PickersDayProps } from '@mui/x-date-pickers';
import type { Dayjs } from 'dayjs';
import React from 'react';

import { StyledPickersDay } from './StyledComponents';

interface DayProps extends PickersDayProps<Dayjs> {
  from: Dayjs | null;
  to: Dayjs | null;
  firstTurn: boolean;
  hoveredDate: Dayjs | null;
  handleMouseOver: (day: Dayjs) => void;
  handleMouseLeave: () => void;
}

export const PuiDateCalendarDay: React.FC<DayProps> = ({
  from,
  to,
  day,
  firstTurn,
  hoveredDate,
  handleMouseOver,
  handleMouseLeave,
  ...other
}) => {
  const isInRange = (day: Dayjs) => {
    return day.isAfter(from) && day.isBefore(to);
  };

  const isInHoveredRange = (day: Dayjs) => {
    if (isInRange(day)) return false;

    if (from && !firstTurn) {
      return day.isAfter(from) && (day.isBefore(hoveredDate) || day.isSame(hoveredDate));
    }

    if (to && firstTurn) {
      return day.isBefore(from) && (day.isAfter(hoveredDate) || day.isSame(hoveredDate));
    }

    return false;
  };
  const classNames =
    `${day.isSame(day.startOf('month'), 'day') ? 'start-of-month' : ''} ` +
    `${day.isSame(day.endOf('month'), 'day') ? 'end-of-month' : ''} ` +
    `${day.day() === 0 ? 'start-of-week' : ''} ` +
    `${day.day() === 6 ? 'end-of-week' : ''} ` +
    `${isInRange(day) ? 'in-range' : ''} ` +
    `${isInHoveredRange(day) ? 'in-hover-range' : ''} `;

  return (
    <StyledPickersDay
      className={classNames}
      {...other}
      day={day}
      disableMargin
      autoFocus={false}
      selected={day.isSame(from, 'day') || day.isSame(to, 'day')}
      onMouseOver={() => handleMouseOver(day)}
      onMouseLeave={handleMouseLeave}
    />
  );
};
