import type { FC } from 'react';

import { ComponentMode } from '../../ui';
import type { IPuiTimePickerProps } from '../common';
import { PuiTimePickerEdit } from './edit/TimePickerEdit';
import { PuiTimePickerView } from './view/TimePickerView';

export const PuiTimePicker: FC<IPuiTimePickerProps> = ({ mode = ComponentMode.Edit, ...props }) => {
  if (mode === ComponentMode.Edit) {
    return <PuiTimePickerEdit {...props} />;
  }

  return <PuiTimePickerView {...props} />;
};
