import { ComponentMode } from '../../ui';
import { PuiDueDatePickerEdit } from './PuiDueDateEdit';
import { PuiDueDatePickerView } from './PuiDueDateView';
import type { DueDateResult, FormatType } from './types';

export interface PuiDueDateProps {
  displayType?: FormatType;
  value?: DueDateResult | null;
  onChange?: (value: DueDateResult) => void;
  border?: boolean;
  mode?: ComponentMode;
  disabled?: boolean;
  isCompleted?: boolean;
  completedTimestamp?: string;
  open?: boolean;
  onOpen?: () => void;
  onClose?: () => void;
}

export const PuiDueDate: React.FC<PuiDueDateProps> = ({
  displayType,
  value = null,
  border,
  mode = ComponentMode.Edit,
  disabled = false,
  isCompleted,
  completedTimestamp,
  ...restProps
}) => {
  return mode === ComponentMode.View || disabled ? (
    <PuiDueDatePickerView
      displayType={displayType}
      value={value}
      border={border}
      isCompleted={isCompleted}
      completedTimestamp={completedTimestamp}
    />
  ) : (
    <PuiDueDatePickerEdit
      displayType={displayType}
      value={value}
      border={border}
      isCompleted={isCompleted}
      completedTimestamp={completedTimestamp}
      {...restProps}
    />
  );
};
