import type { DateFormatType, DateTimeFormatType } from '../config';
import { DATE_TIME_FORMATS } from '../config';

const monthNames = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

const dayNames = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];

const padZero = (value: number) => {
  return value < 10 ? `0${value}` : value;
};

export const getDisplayDate = (date: Date | null, formatType: DateFormatType | DateTimeFormatType): string => {
  if (date) {
    const day: number | string = padZero(date.getDate());
    const month: number | string = padZero(date.getMonth() + 1);
    const hours: number | string = padZero(date.getHours());
    const minutes: number | string = padZero(date.getMinutes());
    const year = date.getFullYear();

    const fullMonth = monthNames[date.getMonth()];
    const dayOfWeek = dayNames[date.getDay()];

    switch (formatType) {
      case 'date_short_1':
        return `${day}/${month}/${year}`;
      case 'date_short_3':
        return `${day}/${month}`;
      case 'date_full_1':
        return `${day}/${fullMonth}/${year}`;
      case 'date_full_2':
        return `${dayOfWeek}, ${day}/${fullMonth}/${year}`;
      case 'datetime_short_1':
        return `${day}/${month}/${year} ${hours}:${minutes}`;
      case 'datetime_full_1':
        return `${day}/${fullMonth}/${year} ${hours}:${minutes}`;
      default:
        return `${day}/${month}/${year}`;
    }
  }

  return DATE_TIME_FORMATS[formatType];
};
