import { PuiBox, PuiIcon, PuiSvgIcon } from '../../../ui';
import { DateInputContainer, StyledArrowContainer, StyledDateField } from '../../common/date-picker/StyledComponents';
import { DATE_TIME_FORMATS } from '../../config';
import type { PuiDateRangeInputProps } from '../types';

const classNames = {
  date_short_1: 'date-short',
  date_short_2: 'date-medium',
  date_full_3: 'date-long',
};

const formats = {
  short: 'date_short_1',
  medium: 'date_short_2',
  long: 'date_full_3',
};

export const PuiDateRangeStandard: React.FC<PuiDateRangeInputProps> = ({
  onFromChange,
  onToChange,
  onFromClick,
  onToClick,
  from,
  to,
  isOpen,
  disabled = false,
  displayType = 'short',
  startDateRef,
  endDateRef,
  placeholder: placeholderValue = '',
}) => {
  const editMode = isOpen || from || to;
  const formatType = (displayType as 'short' | 'medium' | 'long') ?? 'short';

  const dateFormat = formats[formatType] as 'date_short_1' | 'date_short_2' | 'date_full_3';
  const placeholder = editMode ? DATE_TIME_FORMATS[dateFormat] || DATE_TIME_FORMATS.date_short_1 : '';
  const formatClass = classNames[dateFormat];
  const format = DATE_TIME_FORMATS[dateFormat];

  return (
    <DateInputContainer className='standard'>
      <PuiBox className='date-field'>
        <StyledDateField
          className={`${formatClass}`}
          disabled={disabled}
          format={format}
          onClick={onFromClick}
          value={from}
          onChange={onFromChange}
          InputProps={{
            placeholder: placeholder || placeholderValue,
            inputRef: startDateRef,
          }}
        />
        <StyledArrowContainer className={`${disabled ? 'date-disabled' : ''} date-align-start`}>
          {from?.isValid() && to?.isValid() && (
            <PuiSvgIcon
              icon={PuiIcon.ArrowNarrowRight}
              width={6}
              height={6}
            />
          )}
        </StyledArrowContainer>
        <StyledDateField
          className={formatClass}
          disabled={disabled}
          format={format}
          onClick={onToClick}
          value={to}
          onChange={onToChange}
          InputProps={{
            placeholder: placeholder,
            inputRef: endDateRef,
          }}
        />
      </PuiBox>
    </DateInputContainer>
  );
};
