import { DatePicker } from '@mui/x-date-pickers/DatePicker';

import type { IPuiDatePickerProps } from '../../common';
import { BasePicker } from '../../common';
import { DEFAULT_DATE_FORMAT } from '../../config';

export function PuiDatePickerEdit(props: Readonly<IPuiDatePickerProps>) {
  return (
    <BasePicker
      PickerComponent={DatePicker}
      format={props.format ?? DEFAULT_DATE_FORMAT}
      {...props}
    />
  );
}
