import { Popper } from '@mui/material';
import { TimePicker } from '@mui/x-date-pickers';
import type { ElementType } from 'react';

import { PuiStyled } from '../../../ui';

export const StyledPopper: ElementType = PuiStyled(Popper)(({ theme }) => ({
  zIndex: 2000,
  backgroundColor: theme.palette.common.white,
  borderRadius: '4px',
  boxShadow: '0px -4px 12px 0px rgba(0, 0, 0, 0.05), -4px 8px 16px -2px rgba(27, 33, 44, 0.12)',
}));

export const StyledTimePicker: ElementType = PuiStyled(TimePicker)(() => ({
  width: '100%',
  height: '100%',
}));
