import type { BoxProps } from '@mui/material';
import type { FC } from 'react';
import { useMemo } from 'react';

import { PuiBox, PuiStyled, PuiTypography, useTheme } from '../../../ui';
import { convertToDate } from '../../common';
import type { DateFormatType, DateTimeFormatType, DateValueType } from '../../config';
import { getDisplayDate } from '../../utils';

interface DatetimeOptimizedComponentProps {
  isReadOnly?: boolean;
  value?: DateValueType;
  format: DateFormatType | DateTimeFormatType;
  sx?: BoxProps['sx'];
}

interface WrapperProps {
  cursor: 'default' | 'pointer';
}

export const PuiDatePickerView: FC<DatetimeOptimizedComponentProps> = ({ isReadOnly, value, format, sx }) => {
  const theme = useTheme();
  const dateValue = convertToDate(value ?? null);
  const dateTimeText = useMemo(() => getDisplayDate(dateValue, format), [dateValue, format]);

  return (
    <Wrapper
      cursor={isReadOnly ? 'default' : 'pointer'}
      sx={sx}
    >
      <PuiTypography
        variant='body-sm-regular'
        className={`date-time-text ${dateValue ? 'visible' : ''}`}
        color={dateValue ? theme.palette.common.black : theme.palette.grey[200]}
      >
        {dateTimeText}
      </PuiTypography>
    </Wrapper>
  );
};

const Wrapper = PuiStyled(PuiBox)<WrapperProps>(({ cursor, theme }) => ({
  display: 'flex',
  alignItems: 'center',
  padding: '10px 21px 10px 16px',
  justifyContent: 'space-between',
  height: '100%',
  boxSizing: 'border-box',
  cursor: 'text',
  '& .date-time-text': {
    opacity: 0,
    transition: 'opacity 0.1s',
    '&.visible': {
      opacity: 1,
    },
  },
  '&': {
    ':hover': {
      outline: `1px solid ${theme.palette.primary.main}`,
      borderRadius: '4px',
      '& .date-time-text': {
        opacity: 1,
      },
    },
    p: {
      textOverflow: 'clip',
      whiteSpace: 'nowrap',
      overflow: 'hidden',
    },
    svg: {
      cursor: cursor,
    },
  },
}));
