import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';

import { DATE_TIME_FORMATS } from '../config';

dayjs.extend(customParseFormat);

const formats = [
  DATE_TIME_FORMATS.date_short_1,
  DATE_TIME_FORMATS.date_short_2,
  DATE_TIME_FORMATS.date_short_3,
  DATE_TIME_FORMATS.date_full_1,
  DATE_TIME_FORMATS.date_full_2,
  DATE_TIME_FORMATS.date_full_3,
  DATE_TIME_FORMATS.datetime_short_1,
  DATE_TIME_FORMATS.datetime_full_1,
];

export const valueToDateString = (value: string): string | null => {
  for (const format of formats) {
    const date = dayjs(value, format, true);

    if (date.isValid()) {
      return date.utc().format('YYYY-MM-DDTHH:mm:ss[Z]');
    }
  }

  return null;
};
