import type { FC } from 'react';

import { ComponentMode } from '../../ui';
import type { IPuiDatePickerProps } from '../common';
import { DateFormats } from '../config';
import { PuiDatePickerEdit } from './edit/DatePickerEdit';
import { PuiDatePickerView } from './view/DatePickerView';

export const PuiDatePicker: FC<IPuiDatePickerProps> = ({ mode = ComponentMode.Edit, isReadOnly = false, ...props }) => {
  if (mode === ComponentMode.Edit) {
    return <PuiDatePickerEdit {...props} />;
  }

  return (
    <PuiDatePickerView
      {...props}
      isReadOnly={isReadOnly}
      format={props.format ?? DateFormats.date_full_1}
    />
  );
};
