import type { ElementType } from 'react';

import { PuiBox, PuiStyled } from '../../../';

interface StyledTimeViewProps {
  locked?: boolean;
}

export const StyledTimeView: ElementType = PuiStyled(PuiBox, {
  shouldForwardProp: prop => prop !== 'locked',
})<StyledTimeViewProps>(({ theme, locked }) => ({
  cursor: 'pointer',
  fontFamily: 'Poppins',
  height: '100%',
  width: '100%',
  padding: '10px 15px',
  fontSize: '12px',
  display: 'flex',
  justifyContent: 'start',
  alignItems: 'center',
  borderRadius: '4px',
  color: locked ? theme.palette.text.disabled : theme.palette.text.primary,
  '&:hover': {
    color: theme.palette.primary.main,
    '& .timeview-placeholder': {
      color: theme.palette.grey['200'],
    },
  },
  '& .timeview-placeholder': {
    color: 'transparent',
  },
}));
