import type { Dayjs } from 'dayjs';

import { PuiBox, PuiIcon, PuiSvgIcon, PuiTypography } from '../../../ui';
import { DateInputContainer, StyledArrowContainer } from '../../common/date-picker/StyledComponents';
import { FormatType } from '../types';
import { DueDateStatus } from './DueDateStatus';
import { DueDateStatusBox } from './StyledComponents';
import { getFormattedDateString } from './utils';

export interface PuiDueDateViewProps {
  from: Dayjs | null;
  to: Dayjs | null;
  displayType?: FormatType;
  isCompleted?: boolean;
  completedTimestamp?: string;
}

export const PuiDueDateView: React.FC<PuiDueDateViewProps> = ({
  from,
  to,
  displayType = FormatType.Short,
  isCompleted,
  completedTimestamp,
}) => {
  const fromValueString = from ? getFormattedDateString(from, displayType) : '';
  const toValueString = to ? getFormattedDateString(to, displayType) : '';

  const hasStatusData = typeof isCompleted === 'boolean' && typeof completedTimestamp === 'string';

  if (!from && !to) {
    return (
      <DateInputContainer className='show-placeholder'>
        <PuiTypography className='date-placeholder'>Select due date</PuiTypography>
      </DateInputContainer>
    );
  }

  return (
    <DateInputContainer className='standard'>
      <PuiBox className='date-field'>
        {hasStatusData && (
          <DueDateStatusBox>
            <DueDateStatus
              from={from}
              to={to}
              isCompleted={isCompleted}
              completedTimestamp={completedTimestamp}
            />
          </DueDateStatusBox>
        )}
        <PuiTypography
          sx={{ flexShrink: 0 }}
          variant='body-sm-regular'
          className={`date-time-text ${fromValueString ? 'visible' : ''}`}
        >
          {fromValueString}
        </PuiTypography>
        {from?.isValid() && to?.isValid() && (
          <StyledArrowContainer className='date-align-start'>
            <PuiSvgIcon
              icon={PuiIcon.ArrowNarrowRight}
              width={6}
              height={6}
            />
          </StyledArrowContainer>
        )}
        <PuiTypography
          sx={{ flexShrink: 0 }}
          variant='body-sm-regular'
          className={`date-time-text ${toValueString ? 'visible' : ''}`}
        >
          {toValueString}
        </PuiTypography>
      </PuiBox>
    </DateInputContainer>
  );
};
