import type { Dayjs } from 'dayjs';
import dayjs from 'dayjs';

import type { DateValueType } from '../config';

type ValueType = DateValueType | undefined | null | Dayjs;
type ReturnType = Dayjs | undefined | null;

export const formatDateTime = (value: ValueType): ReturnType =>
  (value && !dayjs.isDayjs(value)) || typeof value === 'string' ? dayjs(value) : value;

export const formatTime = (timeString: string, ampm: boolean): string => {
  const date = new Date(`1970-01-01T${timeString}`);

  return date.toLocaleTimeString('en-US', {
    hour: '2-digit',
    minute: '2-digit',
    hour12: ampm,
  });
};

export const convertToDate = (value: DateValueType): Date | null => {
  if (!value) {
    return null;
  }

  if (value instanceof Date) {
    return value;
  }

  if (dayjs.isDayjs(value)) {
    return (value as Dayjs).toDate();
  }

  if (typeof value === 'string') {
    const date = new Date(value);

    return isNaN(date.getTime()) ? null : date;
  }

  return null;
};
