import { DateTimePicker } from '@mui/x-date-pickers';
import type { FC } from 'react';

import { ComponentMode } from '../../ui';
import type { IPuiDateTimePickerProps } from '../common';
import { BasePicker } from '../common';
import { DateFormats, DEFAULT_DATE_TIME_FORMAT } from '../config';
import { PuiDatePickerView } from '../date-picker';

export const PuiDateTimePicker: FC<IPuiDateTimePickerProps> = ({ mode = ComponentMode.Edit, ...props }) => {
  if (mode === ComponentMode.Edit) {
    return (
      <BasePicker
        PickerComponent={DateTimePicker}
        format={props.format ?? DEFAULT_DATE_TIME_FORMAT}
        {...props}
      />
    );
  }

  return (
    <PuiDatePickerView
      {...props}
      format={props.format ?? DateFormats.date_full_1}
    />
  );
};
