import { ClickAwayListener } from '@mui/material';
import type { PickersDayProps } from '@mui/x-date-pickers';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import clsx from 'clsx';
import type { Dayjs } from 'dayjs';
import dayjs from 'dayjs';
import React, { useCallback, useEffect, useRef, useState } from 'react';

import { PuiDateCalendarDay } from '../common/date-calendar-day';
import {
  DateRangeContainer,
  DueDateViewContainer,
  StyledDateCalendar,
  StyledPopper,
  StyledPopperContainer,
} from '../common/date-picker/StyledComponents';
import { PuiDueDateView } from './common/DueDateView';
import { useEnterPress } from './hooks/useEnterPress';
import type { DueDateResult, FormatType } from './types';

interface DateRangeDayjs {
  from: Dayjs | null;
  to: Dayjs | null;
}

export interface PuiDueDateEditProps {
  displayType?: FormatType;
  value?: DueDateResult | null;
  isCompleted?: boolean;
  completedTimestamp?: string;
  border?: boolean;
  open?: boolean;
  onChange?: (value: DueDateResult) => void;
  onOpen?: () => void;
  onClose?: () => void;
}

export const PuiDueDatePickerEdit: React.FC<PuiDueDateEditProps> = ({
  displayType,
  value = null,
  border,
  isCompleted,
  completedTimestamp,
  open,
  onChange,
  onOpen,
  onClose,
}) => {
  const textFieldRef = useRef<HTMLDivElement>(null);
  const [dateValue, setDateValue] = useState<DateRangeDayjs>({ from: null, to: null });
  const [firstTurn, setFirstTurn] = useState(true);
  const [hoveredDate, setHoveredDate] = useState<Dayjs | null>(null);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    const hasOpenProp = open !== undefined;
    const hasTextFieldRef = textFieldRef.current !== null;

    if (hasOpenProp && hasTextFieldRef) {
      setIsOpen(true);
    }
  }, [open]);

  useEffect(() => {
    if (value) {
      setDateValue({
        from: value.from ? dayjs(value.from) : null,
        to: value.to ? dayjs(value.to) : null,
      });
    }
  }, [value]);

  const handleValueChanges = useCallback(
    (value: DateRangeDayjs) => {
      onChange?.({
        from: value.from?.isValid() ? value.from.toDate() : null,
        to: value.to?.isValid() ? value.to.toDate() : null,
      });
    },
    [onChange]
  );

  const handleDateChangeTurnBased = useCallback(
    (date: Dayjs) => {
      setDateValue(prev => {
        let next = { ...prev };

        if (firstTurn) {
          next.from = date;
          if (date.isAfter(prev.to) || date.isSame(prev.to)) {
            next.to = null;
          }
          setFirstTurn(false);
        } else if (date.isBefore(prev.from) || date.isSame(prev.from)) {
          next = { from: date, to: null };
          setFirstTurn(false);
        } else {
          next.to = date;
          setFirstTurn(true);
        }

        handleValueChanges(next);

        return next;
      });
    },
    [firstTurn, handleValueChanges]
  );

  const handleClose = useCallback(() => {
    setIsOpen(false);
    setFirstTurn(true);
    setHoveredDate(null);
    onClose?.();
  }, [onClose]);

  const handleOpen = useCallback(() => {
    setIsOpen(true);
    if (!isOpen) {
      onOpen?.();
    }
  }, [isOpen, onOpen]);

  const Day = useCallback(
    (props: PickersDayProps<Dayjs>) => {
      const { day, ...other } = props;

      return (
        <PuiDateCalendarDay
          day={day}
          {...other}
          from={dateValue.from}
          to={dateValue.to}
          hoveredDate={hoveredDate}
          firstTurn={firstTurn}
          handleMouseOver={() => setHoveredDate(day)}
          handleMouseLeave={() => setHoveredDate(null)}
        />
      );
    },
    [dateValue.from, dateValue.to, hoveredDate, firstTurn]
  );

  useEnterPress(() => {
    if (isOpen) {
      handleClose();
    }
  });

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>
      <ClickAwayListener onClickAway={handleClose}>
        <DateRangeContainer>
          <DueDateViewContainer
            className={clsx({ 'due-date-border': border, opened: isOpen })}
            onClick={handleOpen}
            ref={textFieldRef}
          >
            <PuiDueDateView
              from={dateValue.from}
              to={dateValue.to}
              displayType={displayType}
              isCompleted={isCompleted}
              completedTimestamp={completedTimestamp}
            />
          </DueDateViewContainer>
          <StyledPopper
            open={isOpen}
            anchorEl={textFieldRef.current}
            placement='bottom-start'
            modifiers={[{ name: 'offset', options: { offset: [0, 4] } }]}
          >
            <StyledPopperContainer>
              <StyledDateCalendar
                onChange={handleDateChangeTurnBased}
                slots={{ day: Day }}
              />
            </StyledPopperContainer>
          </StyledPopper>
        </DateRangeContainer>
      </ClickAwayListener>
    </LocalizationProvider>
  );
};
