import type { Dayjs } from 'dayjs';
import dayjs from 'dayjs';
import { useMemo } from 'react';

import { DateRangeContainer, DateRangeInputContainer } from '../../common/date-picker/StyledComponents';
import type { PuiDateRangeProps } from '../types';
import { PuiViewDateRangePill } from './ViewDateRangePill';
import { PuiViewDateRangeStandard } from './ViewDateRangeStandard';

interface DateRangeDayjs {
  from: Dayjs | null;
  to: Dayjs | null;
}

export const PuiDateRangeView: React.FC<PuiDateRangeProps> = ({
  displayType,
  value = null,
  border,
  disabled = false,
  sx,
}) => {
  const dateValue = useMemo<DateRangeDayjs>(() => {
    return {
      from: value?.from ? dayjs(value.from) : null,
      to: value?.to ? dayjs(value.to) : null,
    };
  }, [value]);

  const classNames = `${border ? 'date-range-border' : ''}`;

  return (
    <DateRangeContainer
      sx={sx}
      className='date-range-container'
    >
      <DateRangeInputContainer className={`${classNames} date-range-input-container`}>
        {displayType === 'pill' ? (
          <PuiViewDateRangePill
            from={dateValue.from}
            to={dateValue.to}
            disabled={disabled}
          />
        ) : (
          <PuiViewDateRangeStandard
            from={dateValue.from}
            to={dateValue.to}
            disabled={disabled}
            displayType={displayType}
          />
        )}
      </DateRangeInputContainer>
    </DateRangeContainer>
  );
};
