import dayjs from 'dayjs';

import type { IPuiTimePickerProps } from '../../common';
import { BasePicker } from '../../common';
import { TIME_FORMATS } from '../../config';
import { StyledTimePicker } from './StyledComponents';

export const PuiTimePickerEdit = ({ ampm = false, value, ...props }: IPuiTimePickerProps) => {
  const format = ampm ? TIME_FORMATS.ampm : TIME_FORMATS.nonAmpm;

  return (
    <BasePicker
      PickerComponent={StyledTimePicker}
      format={format}
      views={['hours', 'minutes']}
      ampm={ampm}
      value={value ? dayjs(`1970-01-01T${value}`) : null}
      slots={{ actionBar: undefined }}
      {...props}
    />
  );
};
