import { ComponentMode } from '../../ui';
import { PuiDateRangeEdit } from './edit/DateRangeEdit';
import type { PuiDateRangeProps } from './types';
import { PuiDateRangeView } from './view/DateRangeView';

export const PuiDateRange: React.FC<PuiDateRangeProps> = ({
  displayType,
  value = null,
  border,
  mode = ComponentMode.Edit,
  disabled = false,
  sx,
  ...restProps
}) => {
  return mode === ComponentMode.View || disabled ? (
    <PuiDateRangeView
      displayType={displayType}
      value={value}
      border={border}
      disabled={disabled}
      sx={sx}
    />
  ) : (
    <PuiDateRangeEdit
      displayType={displayType}
      value={value}
      border={border}
      {...restProps}
    />
  );
};
