import { FrownDueDate, PuiBox, PuiTooltip } from '../../../../ui';
import { DaysIcon } from './StyledComponents';

type StatusDaysIconProps = {
  value: number;
  isOverdue?: boolean;
};

export const StatusDaysIcon: React.FC<StatusDaysIconProps> = ({ value, isOverdue = false }) => {
  const getTooltipText = (): string => {
    const dayText = value === 1 ? 'day' : 'days';

    if (isOverdue) {
      return `${value} ${dayText} overdue`;
    }

    if (value === 0) {
      return 'Due is today';
    }

    return `Due in ${value} ${dayText}`;
  };

  const tooltipText = getTooltipText();
  const showFrownFace = value > 60;

  return (
    <PuiTooltip
      title={tooltipText}
      placement='top'
      arrow
    >
      {showFrownFace ? (
        <PuiBox>
          <FrownDueDate />
        </PuiBox>
      ) : (
        <DaysIcon className={isOverdue ? 'days-overdue' : ''}>{value}</DaysIcon>
      )}
    </PuiTooltip>
  );
};
