import type { Dayjs } from 'dayjs';
import dayjs from 'dayjs';
import { useCallback, useEffect, useState } from 'react';

import { PuiBox, PuiIcon, PuiSvgIcon } from '../../../ui';
import {
  DateViewPillContainer,
  ProgressFill,
  StyledArrowContainer,
  StyledDatePlaceholder,
  StyledPillPlaceholder,
  StyledPillViewDateField,
} from '../../common/date-picker/StyledComponents';

export interface PuiViewDateRangeStandardProps {
  from: Dayjs | null;
  to: Dayjs | null;
  disabled: boolean;
  displayType?: string;
}

export const PuiViewDateRangePill: React.FC<PuiViewDateRangeStandardProps> = ({ from, to }) => {
  const dateDifference = from && to ? to.diff(from, 'day') : 0;
  const dateDifferenceString = dateDifference > 0 ? `${dateDifference} ${dateDifference > 1 ? 'days' : 'day'}` : '';
  const placeholder = dateDifference > 0 ? dateDifferenceString : '';
  const format = 'DD/MM';
  const showPlaceholder = dateDifference > 0;
  const classNames = `pill ${!from && !to ? 'date-empty' : ''} ${showPlaceholder ? 'show-placeholder' : ''}`;

  const [dateProgress, setDateProgress] = useState(0);

  const calculateProgress = useCallback(() => {
    if (!from || !to) {
      setDateProgress(0);

      return;
    }

    const now = dayjs();

    if (from.isBefore(now) && to.isBefore(now)) {
      setDateProgress(100);

      return;
    }

    let progress = 0;

    if (now.isBefore(to, 'day') || now.isSame(to, 'day')) {
      const fromNowDifference = now.diff(from, 'hour');
      const fromToDifference = to.diff(from, 'hour');
      progress = Math.min((fromNowDifference / fromToDifference) * 100, 100);
    }
    setDateProgress(progress);
  }, [from, to]);

  useEffect(() => {
    calculateProgress();
  }, [from, to, calculateProgress]);

  return (
    <>
      {!from && !to ? (
        <StyledDatePlaceholder className='empy-date-placeholder'>Select date range</StyledDatePlaceholder>
      ) : (
        <DateViewPillContainer className={classNames}>
          <ProgressFill style={{ width: `${dateProgress}%` }} />
          <PuiBox className='date-field'>
            <StyledPillViewDateField>{from ? from.format(format) : ''}</StyledPillViewDateField>
            <StyledArrowContainer className='justify-icon-center'>
              {from?.isValid() && to?.isValid() && (
                <PuiSvgIcon
                  icon={PuiIcon.ArrowNarrowRight}
                  width={6}
                  height={6}
                />
              )}
            </StyledArrowContainer>
            <StyledPillViewDateField>{to ? to.format(format) : ''}</StyledPillViewDateField>
          </PuiBox>
          <StyledPillPlaceholder className='date-placeholder'>{placeholder}</StyledPillPlaceholder>
        </DateViewPillContainer>
      )}
    </>
  );
};
