import { PuiBox } from '../../../ui';
import { formatTime } from '../../common';
import { StyledTimeView } from './StyledComponents';

interface TimeViewProps {
  value?: string;
  locked?: boolean;
  onClick?: React.MouseEventHandler<HTMLDivElement>;
  ampm?: boolean;
}

export const PuiTimePickerView: React.FC<TimeViewProps> = ({ value, locked, onClick, ampm = false }) => {
  let formattedTime;
  if (value) {
    formattedTime = formatTime(value, ampm);
  } else {
    formattedTime = ampm ? 'hh:mm aa' : 'hh:mm';
  }

  const className = value ? '' : 'timeview-placeholder';

  return (
    <StyledTimeView
      onClick={onClick}
      locked={locked}
    >
      <PuiBox className={className}>{formattedTime}</PuiBox>
    </StyledTimeView>
  );
};
