import { ClickAwayListener } from '@mui/material';
import type { PickersDayProps } from '@mui/x-date-pickers';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import type { Dayjs } from 'dayjs';
import dayjs from 'dayjs';
import React, { useCallback, useEffect, useRef, useState } from 'react';

import { PuiDateCalendarDay } from '../../common/date-calendar-day';
import {
  DateRangeContainer,
  DateRangeInputContainer,
  StyledDateCalendar,
  StyledPopper,
  StyledPopperContainer,
} from '../../common/date-picker/StyledComponents';
import { PuiDateRangePill } from '../common/DateRangePill';
import { PuiDateRangeStandard } from '../common/DateRangeStandard';
import type { PuiDateRangeProps } from '../types';

interface DateRangeDayjs {
  from: Dayjs | null;
  to: Dayjs | null;
}

export const PuiDateRangeEdit: React.FC<PuiDateRangeProps> = ({
  displayType,
  onChange,
  value = null,
  border,
  open = false,
  onOpen,
  onClose,
  placeholder,
}) => {
  const textFieldRef = useRef<HTMLDivElement>(null);
  const [dateValue, setDateValue] = useState<DateRangeDayjs>({ from: null, to: null });
  const [firstTurn, setFirstTurn] = useState(true);
  const [hoveredDate, setHoveredDate] = useState<Dayjs | null>(null);
  const [activeInputValue, setActiveInputValue] = useState<Dayjs | null>(null);
  const [invalid, setInvalid] = useState(false);
  const [isOpen, setIsOpen] = useState(false);

  const startDateRef = useRef<HTMLDivElement>(null);
  const endDateRef = useRef<HTMLDivElement>(null);

  const isRangeInvalid = useCallback((value: DateRangeDayjs) => {
    return Boolean(value?.from?.isValid() && value?.to?.isValid() && value.from.isAfter(value.to, 'day'));
  }, []);

  useEffect(() => {
    if (value) {
      setDateValue({ from: value.from ? dayjs(value.from) : null, to: value.to ? dayjs(value.to) : null });
    }
  }, [value]);

  const handleValueChanges = useCallback(
    (value: DateRangeDayjs) => {
      if (onChange) {
        const difference = value.from?.isValid() && value.to?.isValid() ? value.to.diff(value.from, 'hour') : 0;
        onChange({
          from: value.from?.isValid() ? value.from.toDate() : null,
          to: value.to?.isValid() ? value.to.toDate() : null,
          difference: difference,
        });
      }
    },
    [onChange]
  );

  const setStartDateIfUnset = useCallback(() => {
    if (dateValue.to?.isValid() && !dateValue.from) {
      dateValue.from = dateValue.to.startOf('month');
      setDateValue({ ...dateValue, from: dateValue.from });
      if (onChange) {
        handleValueChanges(dateValue);
      }
    }
  }, [dateValue, onChange, handleValueChanges]);

  useEffect(() => {
    setStartDateIfUnset();
    setInvalid(isRangeInvalid(dateValue));
  }, [dateValue, isRangeInvalid, setStartDateIfUnset]);

  const focusActiveInput = useCallback(() => {
    if (!dateValue.from) {
      startDateRef?.current?.focus();
    } else {
      endDateRef?.current?.focus();
    }
  }, [startDateRef, endDateRef, dateValue]);

  const openCalendarPopup = useCallback(() => {
    focusActiveInput();
  }, [focusActiveInput]);

  const handleClick = useCallback(() => {
    if (!open) {
      onOpen?.();
      setFirstTurn(!dateValue.from);
    }
  }, [open, onOpen, dateValue.from]);

  const closeCalendarPopup = useCallback(() => {
    setFirstTurn(true);
    setHoveredDate(null);
    setActiveInputValue(null);
  }, []);

  useEffect(() => {
    if (open && textFieldRef.current) {
      setIsOpen(true);
      openCalendarPopup();
    } else {
      setIsOpen(false);
      closeCalendarPopup();
    }
  }, [open, openCalendarPopup, closeCalendarPopup, textFieldRef]);

  const handleDateChangeTurnBased = useCallback(
    (date: Dayjs) => {
      const value = { ...dateValue };
      if (firstTurn) {
        value.from = date;
        if (date.isAfter(value.to) || date.isSame(value.to)) {
          value.to = null;
        }
        setFirstTurn(false);
      } else if (date.isBefore(value.from) || date.isSame(value.from)) {
        value.to = null;
        value.from = date;
        setFirstTurn(false);
      } else {
        value.to = date;
        setFirstTurn(true);
      }
      setDateValue(value);
      if (onChange) {
        handleValueChanges(value);
      }
    },
    [dateValue, firstTurn, handleValueChanges, onChange]
  );

  const handleClose = useCallback(() => {
    onClose?.();
  }, [onClose]);

  const handleDayOnMouseOver = useCallback((date: Dayjs) => {
    setHoveredDate(date);
  }, []);

  const handleCalendarOnMouseLeave = useCallback(() => {
    setHoveredDate(null);
  }, []);

  const handleDateChange = useCallback(
    (date: Dayjs | null, type: 'from' | 'to') => {
      const value = { ...dateValue, [type]: date };
      setActiveInputValue(date);

      setDateValue(value);
      if (!date || (date?.isValid() && !isRangeInvalid(value) && date.year() > 999)) {
        handleValueChanges(value);
      }
    },
    [dateValue, handleValueChanges, isRangeInvalid]
  );

  const handleValueFromChange = (date: Dayjs | null) => handleDateChange(date, 'from');
  const handleValueToChange = (date: Dayjs | null) => handleDateChange(date, 'to');

  const Day = useCallback(
    (props: PickersDayProps<Dayjs>) => {
      const { day, ...other } = props;

      return (
        <PuiDateCalendarDay
          day={day}
          {...other}
          from={dateValue.from}
          to={dateValue.to}
          hoveredDate={hoveredDate}
          firstTurn={firstTurn}
          handleMouseOver={() => handleDayOnMouseOver(day)}
          handleMouseLeave={handleCalendarOnMouseLeave}
        />
      );
    },
    [dateValue.from, dateValue.to, firstTurn, handleCalendarOnMouseLeave, handleDayOnMouseOver, hoveredDate]
  );

  const handleKeyUp = useCallback(
    (event: React.KeyboardEvent) => {
      if (event.key === 'Enter') {
        onClose?.();
      }
    },
    [onClose]
  );

  const classNames = `${border ? 'date-range-border' : ''} ${open ? 'opened' : ''} ${invalid ? 'date-range-invalid' : ''}`;

  useEffect(() => {
    if (open && dayjs(dateValue?.from).isValid() && dateValue?.from && dateValue?.from?.year() > 999 && !dateValue.to) {
      endDateRef?.current?.focus();
    }
  }, [dateValue?.from, dateValue.to, open]);

  useEffect(() => {
    if (open && dateValue?.from) {
      setActiveInputValue(dateValue.from);
    }
  }, [open, dateValue?.from]);

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>
      <ClickAwayListener onClickAway={handleClose}>
        <DateRangeContainer>
          <DateRangeInputContainer
            className={classNames}
            ref={textFieldRef}
            onKeyUp={handleKeyUp}
            onClick={handleClick}
          >
            {displayType === 'pill' ? (
              <PuiDateRangePill
                from={dateValue.from}
                to={dateValue.to}
                viewMode={false}
                onFromChange={handleValueFromChange}
                onToChange={handleValueToChange}
                isOpen={isOpen}
                disabled={false}
                onFromClick={() => setFirstTurn(true)}
                onToClick={() => setFirstTurn(false)}
                startDateRef={startDateRef}
                endDateRef={endDateRef}
                placeholder={placeholder}
              />
            ) : (
              <PuiDateRangeStandard
                from={dateValue.from}
                to={dateValue.to}
                isOpen={isOpen}
                viewMode={false}
                disabled={false}
                onFromChange={handleValueFromChange}
                onToChange={handleValueToChange}
                onFromClick={() => setFirstTurn(true)}
                onToClick={() => setFirstTurn(false)}
                displayType={displayType}
                startDateRef={startDateRef}
                endDateRef={endDateRef}
                placeholder={placeholder}
              />
            )}
          </DateRangeInputContainer>
          <StyledPopper
            open={isOpen && textFieldRef.current !== null}
            anchorEl={textFieldRef.current}
            placement='bottom-start'
            modifiers={[
              {
                name: 'offset',
                options: {
                  offset: [0, 4],
                },
              },
            ]}
          >
            <StyledPopperContainer>
              <StyledDateCalendar
                disableHighlightToday
                onChange={handleDateChangeTurnBased}
                slots={{ day: Day }}
                value={activeInputValue}
              />
            </StyledPopperContainer>
          </StyledPopper>
        </DateRangeContainer>
      </ClickAwayListener>
    </LocalizationProvider>
  );
};
