import type { Dayjs } from 'dayjs';

import { StatusCompletedIcon, StatusDaysIcon } from '../common/status-icons';

type DueDateStatusProps = {
  from: Dayjs | null;
  to: Dayjs | null;
  isCompleted: boolean;
  completedTimestamp: string;
};

export const DueDateStatus: React.FC<DueDateStatusProps> = ({ from, to, isCompleted, completedTimestamp }) => {
  const dueDate = to || from || null;

  if (dueDate === null) {
    return null;
  }

  if (isCompleted) {
    const completedDate = new Date(completedTimestamp);
    const days = Math.abs(dueDate.diff(completedDate, 'day'));
    const isCompletedAfter = days === 0 ? false : dueDate.isBefore(completedDate);

    return (
      <StatusCompletedIcon
        isCompletedAfter={isCompletedAfter}
        value={days}
      />
    );
  }

  const now = new Date();
  const currentDate = new Date(now.getFullYear(), now.getMonth(), now.getDate());
  const overdueDays = dueDate.diff(currentDate, 'day');
  const isOverdue = overdueDays < 0;
  const days = Math.abs(overdueDays);

  return (
    <StatusDaysIcon
      value={days}
      isOverdue={isOverdue}
    />
  );
};
