import type { Dayjs } from 'dayjs';

import { DATE_TIME_FORMATS } from '../../../datetime';
import { PuiBox, PuiIcon, PuiSvgIcon } from '../../../ui';
import {
  DateInputContainer,
  StyledArrowContainer,
  StyledDatePlaceholder,
  StyledDateViewField,
} from '../../common/date-picker/StyledComponents';

export interface PuiViewDateRangeStandardProps {
  from: Dayjs | null;
  to: Dayjs | null;
  disabled: boolean;
  displayType?: string;
}

const formats = {
  short: 'date_short_1',
  medium: 'date_short_2',
  long: 'date_full_3',
};

const classNames = {
  date_short_1: 'date-short',
  date_short_2: 'date-medium',
  date_full_3: 'date-long',
};

export const PuiViewDateRangeStandard: React.FC<PuiViewDateRangeStandardProps> = ({
  from,
  to,
  disabled = false,
  displayType = 'short',
}) => {
  const formatType = (displayType as 'short' | 'medium' | 'long') ?? 'short';
  const dateFormat = formats[formatType] as 'date_short_1' | 'date_short_2' | 'date_full_3';
  const formatClass = classNames[dateFormat];
  const format = DATE_TIME_FORMATS[dateFormat];

  return (
    <>
      {!from && !to ? (
        <StyledDatePlaceholder className='empy-date-placeholder'>Select date range</StyledDatePlaceholder>
      ) : (
        <DateInputContainer className='standard'>
          <PuiBox className='date-field'>
            <StyledDateViewField className={`${disabled ? 'date-disabled' : ''} ${formatClass}`}>
              {from ? from.format(format) : ''}
            </StyledDateViewField>
            <StyledArrowContainer
              className={`${disabled ? 'date-disabled' : ''} ${displayType != 'pill' ? 'date-align-start' : ''}`}
            >
              {from?.isValid() && to?.isValid() && (
                <PuiSvgIcon
                  icon={PuiIcon.ArrowNarrowRight}
                  width={6}
                  height={6}
                />
              )}
            </StyledArrowContainer>
            <StyledDateViewField className={`${disabled ? 'date-disabled' : ''} ${formatClass}`}>
              {to ? to.format(format) : ''}
            </StyledDateViewField>
          </PuiBox>
        </DateInputContainer>
      )}
    </>
  );
};
