import dayjs from 'dayjs';

import { DateRangeContainer, DueDateViewContainer } from '../common/date-picker/StyledComponents';
import { PuiDueDateView } from './common/DueDateView';
import type { DueDateResult, FormatType } from './types';

export interface PuiDueDateViewProps {
  displayType?: FormatType;
  value?: DueDateResult | null;
  border?: boolean;
  isCompleted?: boolean;
  completedTimestamp?: string;
}

export const PuiDueDatePickerView: React.FC<PuiDueDateViewProps> = ({
  displayType,
  value = null,
  border,
  isCompleted,
  completedTimestamp,
}) => {
  const dateValue = {
    from: value?.from ? dayjs(value.from) : null,
    to: value?.to ? dayjs(value.to) : null,
  };

  return (
    <DateRangeContainer>
      <DueDateViewContainer className={`${border ? 'due-date-border' : ''}`}>
        <PuiDueDateView
          from={dateValue.from}
          to={dateValue.to}
          displayType={displayType}
          isCompleted={isCompleted}
          completedTimestamp={completedTimestamp}
        />
      </DueDateViewContainer>
    </DateRangeContainer>
  );
};
