import type { Dayjs } from 'dayjs';

import { FormatType } from '../types';

export const getFormattedDateString = (date: Dayjs, format: FormatType): string => {
  if (format === FormatType.Short) {
    return date.format('DD/MM/YYYY');
  }

  if (format === FormatType.Medium) {
    return date.format('D MMM YYYY');
  }

  if (format === FormatType.Long) {
    return date.format('ddd, D MMM YYYY');
  }

  return date.format('DD/MM/YYYY');
};
